.card{
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: flex-end;
}

.capa{
    width: 225px;
    border-radius: 8px;
}

.capa:hover{
    transform: scale(1.15);
    transition: transform 0.3s;
    border-radius: 8px;
    box-shadow: 0 0 10px #222;
}

.icon{
    width: 30px;
    height: 30px;
    background-color: #00000050;
    border-radius: 10px;
    padding: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    margin-right: 5px;
    margin-bottom: 5px;
}

.icon:hover{
    background-color: #000000;
    cursor: pointer;
    transform: scale(1.1);
}